/* clickable view */
.modal {
	display: none;
	position: fixed;
	z-index: 1;
	padding-top: 10vh;
	width: 100%;
	height: 100%;
	overflow: auto;
	background-color: rgb(0,0,0);
	background-color: rgba(0,0,0,0.4);
	cursor: pointer;
  }
  
  .modal-content {
	background-color: #fefefe;
	z-index: 2;
	margin: auto;
	padding: 4vh;
	border: .35vh solid rgb(219, 219, 219);
	border-radius: 3.5vh;
	width: 80%;
	height:90%;
	cursor: default ;
  }
  
  .close {
	color: #000000;
	float: right;
	font-size: 5vh;
	font-weight: bold;
	padding-bottom: 2vh;
	padding-right: 2vh;
  }
  
  .close:hover,
  .close:focus {
	color: #000;
	text-decoration: none;
	cursor: pointer;
  }
